module.exports = " <div class=row> <div class=col-lg-12> <h1 class=page-header>New survey</h1> </div> </div> <div class=row> <div class=col-md-12> <button class=\"btn btn-primary\" @click=addSection()><i class=\"fa fa-plus\"></i> Add Section</button> <a class=\"pull-right btn btn-success\"><i class=\"fa fa-save\"></i> Save</a> <hr> </div> </div> <div class=row> <div class=col-md-3> <ul class=\"nav nav-pills nav-stacked\"> <li v-for=\"section in survey.sections\" @mouseover=\"mouseHover=$index\" @mouseout=\"mouseHover=-1\" track-by=$index :class=\"{'active':currentSection==section}\"> <a @click.self=\"currentSection=section\"> {{section.name}} <span class=badge v-show=\"section.questions&&section.questions.length>0\">{{section.questions?section.questions.length:0}}</span> </a> <button style=position:absolute;top:40%;right:3px transition=fade @click.prevent=removeSection(section) v-show=\"mouseHover==$index\" class=\"btn btn-xs btn-danger\"><i class=\"fa fa-remove\"></i></button> </li> </ul> </div> <div v-show=currentSection class=col-md-9 style=background:#fafafa;padding:10px> <div class=row> <div class=col-md-9> <section-name v-if=currentSection&&currentSection.name :name.sync=currentSection.name></section-name> </div> <div class=col-md-3> <div style=margin:10px class=text-right> <add-question v-show=\"currentSection&&currentSection.questions&&currentSection.questions.length>0\" :callback=focusLastQuestion :section.sync=currentSection></add-question> </div> </div> </div> <div v-show=\"currentSection&&currentSection.questions&&currentSection.questions.length>0\" class=text-center style=margin-top:20px;margin-bottom:200px> <div class=row v-for=\"question in currentSection.questions\" track-by=$index transition=item> <div class=\"col-md-12 text-left\"> <div :class=\"{'focused':inputFocus==$index}\" class=\"panel question panel-primary\" v-drag-and-drop drop=handleDrop id=\"{{ $index }}\"> <div class=panel-body> <span class=\"text-muted small\" style=position:absolute;top:10px;left:30px> #{{$index+1}} </span> <div class=col-md-12> <textarea @focus=\"inputFocus=$index\" @blur=\"inputFocus=-100\" type=text name=\"{{'question-'+$index}}\" class=\"form-control question-control\" v-model=question.description> \n                                         </textarea> </div> </div> <div class=\"panel-footer text-right\"> <i class=\"fa fa-arrows pull-left\" style=margin-top:.5em;opacity:0.6></i> <question-type class=pull-left :type.sync=question.type></question-type> <button class=\"btn btn-sm btn-primary\" @click=duplicateQuestion($index)>Duplicate</button> <button style=margin-left:3px data-balloon=\"This action is unrecoverable!!\" data-balloon-pos=down @click=deleteQuestion(question) class=\"btn btn-sm btn-danger\">Delete </button> </div> </div> </div> </div> <div style=margin-top:40px> <add-question :callback=focusLastQuestion :section.sync=currentSection></add-question> </div> </div> <div v-show=\"!currentSection||!currentSection.questions||currentSection.questions.length==0\" class=text-center style=margin-top:100px;margin-bottom:200px> <img src=" + require("../assets/empty.png") + " height=100 alt=\"\"> <br> <br> No question has been added to this section yet~ <br><br> <add-question :callback=focusLastQuestion :section.sync=currentSection></add-question> </div> </div> </div> ";


