<template>
     <div class="row">
                    <div class="col-lg-12">
                        <h1 class="page-header">Dashboard</h1>
                    </div>
                   
     </div>
               
                <div class="row">
                    <div class="col-md-8">
                          <div class="panel panel-primary">
      <div class="panel-heading">  Survey Received Source</div>
      <div class="panel-body">
          <div id="c1"></div>
      </div>
    </div>
                    </div>
                    <div class="col-md-4">
                          <div class="panel panel-primary">
                                    <div class="panel-heading" style="background:#FAFAFA"> Shortcuts</div>

                               <div class="panel-body">

                        <button class="btn btn-block btn-success">Create Survey</button>
                 <button class="btn btn-block btn-primary">Add Contact</button>
                 <hr>
                                         <button class="btn btn-block btn-danger">Need Help?</button>

                          </div>
                          </div>
                    </div>
                     
             </div>
             
              
             
</template>
<script>
    export default {
        data() {
                return {
                    chartdata: [{
                        genre: 'Email',
                        sold: 275
                    }, {
                        genre: 'QRCode',
                        sold: 115
                    }, {
                        genre: 'URL',
                        sold: 120
                    }, {
                        genre: 'Other',
                        sold: 150
                    }, ]
                }
            },
            ready() {
                var chart = new G2.Chart({
                    id: 'c1', //  ID
                    width: 600, // 
                    height: 300, // 
                    fontFamily: 'Open Sans'
                });
                // Step 2: 
                chart.source(this.chartdata, {
                    genre: {
                        alias: 'Type' // 
                    },
                    sold: {
                        alias: 'Done'
                    }
                });
                // Step 3 genre  sold genre  x sold  y 
                chart.interval().position('genre*sold').color('genre')
                    // Step 4: 
                chart.render();
            }

    }
</script>


